<template>
  <div>
    <div class="table-bg">
      <el-table :data="list" style="width: 100%;margin-bottom: 20px;">
        <el-table-column label="用户uid" width="320" prop="uid"></el-table-column>
        <el-table-column label="用户昵称" width="320" prop="nickname"></el-table-column>
        <el-table-column label="手机号" width="320" prop="phone"></el-table-column>
        <el-table-column label="状态" prop="status">
          <template slot-scope="scope">
              <el-tag v-if="scope.row.status==1" type="success">正常</el-tag>
              <el-tag  v-else type="info">禁用</el-tag>
            </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import { getMemberList } from "@/request/member"
export default {
  data() {
    return {
      list:[]
    };
  },
  mounted() {
     this.getlist();
  },
  methods: {
     async getlist(){
        this.list = await getMemberList();
     }
  },
  components: {}
};
</script>
<style scoped>
</style>


